/* lato-300 - latin */
@font-face {
  font-display: swap;
  font-family: 'Lato';
  font-style: normal;
  font-weight: 300;
  src: url('lato-v23-latin/lato-v23-latin-300.woff2') format('woff2'),
    url('lato-v23-latin/lato-v23-latin-300.woff') format('woff');
}
/* lato-300italic - latin */
@font-face {
  font-display: swap;
  font-family: 'Lato';
  font-style: italic;
  font-weight: 300;
  src: url('lato-v23-latin/lato-v23-latin-300italic.woff2') format('woff2'),
    url('lato-v23-latin/lato-v23-latin-300italic.woff') format('woff');
}
